import { Button, createTheme, IconButton, makeStyles, Modal, Paper, TextField } from "@material-ui/core";
import { useState } from "react";
import { AddForm, Container, GridCards, RowAnounceAndGrid } from "../../styles";
import HeaderHome from "../../components/HeaderHome";
import { uuid } from "uuidv4";
import { useEffect } from "react";
import AdSense from 'react-adsense';
import correios from '../../services/correios';

import mapboxgl from "mapbox-gl";
import CardItem from "../../components/CardItem";
import ModalHistoric from "../../components/ModalHistoric";
import CloseIcon from '@material-ui/icons/Close';

function Home() {
  const [openHistoric, setOpenHistoric] = useState(false);
  const [products, setProducts] = useState([]);
  const [product, setProduct] = useState('');
  const [historic, setHistoric] = useState([]);
  const [selectedProduct, setSelectedProduct] = useState({});
  const [selectedHistoric, setSelectedHistoric] = useState([]);
  const [codRastreio, setCodRastreio] = useState('');
  const [cepDestino, setCepDestino] = useState('');

  const theme = createTheme({
    breakpoints: {
      values: {
        md: 426,
      },
    },
  });

  const useStyles = makeStyles({
    input: {
      [theme.breakpoints.up('md')]: {
        width: '20rem',
        fontSize: '1.2rem',

        '& + &': {
          marginLeft: '0.8rem',
          marginTop: '0 !important',
        },
        "& .MuiInputLabel-root": {
          fontSize: '1.2rem !important'
        },
        "& .MuiInputBase-input": {
          fontSize: '1.2rem !important'
        },
        '& label.Mui-focused': {
          color: '#303030',
          fontFamily: 'Ubuntu',
          fontSize: '1.2rem !important'
        },

        '.MuiOutlinedInput-input': {
          fontFamily: 'Ubuntu',
          fontSize: '1.2rem !important'
        },

        '& .MuiOutlinedInput-root': {
          '& fieldset': {
            borderColor: '#303030',
            fontFamily: 'Ubuntu',
            fontSize: '1.2rem !important'
          },
          '&:hover fieldset': {
            borderColor: '#464646',
            fontSize: '1.2rem !important'
          },
          '&.Mui-focused fieldset': {
            borderColor: '#303030',
            fontSize: '1.2rem !important'
          },
        },
      },

      width: '90%',
      fontSize: '10rem',

      '& + &': {
        marginLeft: '0',
        marginTop: '1.5rem',
      },
      "& .MuiInputLabel-root": {
        fontSize: '3.5rem'
      },
      "& .MuiInputBase-input": {
        fontSize: '3.5rem'
      },
      '& label.Mui-focused': {
        color: '#303030',
        fontFamily: 'Ubuntu',
        fontSize: '3.5rem'
      },

      '.MuiOutlinedInput-input': {
        fontFamily: 'Ubuntu',
        fontSize: '3.5rem'
      },

      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: '#303030',
          fontFamily: 'Ubuntu',
          fontSize: '3.5rem'
        },
        '&:hover fieldset': {
          borderColor: '#464646',
          fontSize: '3.5rem'
        },
        '&.Mui-focused fieldset': {
          borderColor: '#303030',
          fontSize: '3.5rem'
        },
      },
    },
    button: {
      [theme.breakpoints.up('md')]: {
        width: '12.5rem',
        backgroundColor: '#303030',
        color: '#FFF',
        font: '400 0.85rem Ubuntu',
        padding: '1rem 4rem',
        borderRadius: '0.438rem',
        marginLeft: '2.125rem',
        marginTop: '0 !important',
        transition: '400ms',

        '&:hover': {
            backgroundColor: '#464646',
        },
      },

      width: '90%',
      backgroundColor: '#303030',
      color: '#FFF',
      font: '400 3.5rem Ubuntu',
      padding: '3rem 4rem',
      borderRadius: '1rem',
      marginLeft: '0',
      marginTop: '3.5rem',
      transition: '400ms',

      '&:hover': {
          backgroundColor: '#464646',
      },
    },
    logoCard: {
      width: '6rem',
      margin: '-0.9rem 0 -0.6rem -1rem',
    },
    paperModal: {
      width: '30rem',
      height: '30rem',
      backgroundColor: '#303030',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      borderRadius: '1rem',

      '&:focus': {
        outline: 'none',
        border: 'none',
      }
    },
  });

  const classes = useStyles();

  function takeProducts() {
    mapboxgl.accessToken = 'pk.eyJ1Ijoic3BleWNlciIsImEiOiJja3JmbHYzeDY1eTNqMm5sMzZ6b3F6YjNxIn0.GUFClPNRuUu50n3aP8Wp5g';

    if (localStorage.getItem('products')) {
      setProducts(JSON.parse(localStorage.getItem('products')))
    }
  }

  function getRastreio(codigo) {
    correios.get('', {
      params: {
        codigo: codigo
      }
    }).then(response => {
      console.log(response)
      return response.data
    })
  }

  function handleOpenHistoric(product) {
    setSelectedProduct(product)
    // setSelectedHistoric(getRastreio(product.codRastreio))
    setOpenHistoric(true)
  }

  function handleCloseHistoric() {
    setOpenHistoric(false)
    setSelectedProduct(null)
  }

  async function registerNewProduct(e) {
    e.preventDefault()

    setHistoric(getRastreio(codRastreio))
    
    var isUnique = true
    
    products.map(product => {
      if (product.codRastreio === codRastreio) {
        return isUnique = false
      } else {
        return isUnique
      }
    })
    
    if (isUnique) {
      var newProduct = {
        id: uuid(),
        product: product,
        codRastreio: codRastreio,
        cepDestino: cepDestino,
        lastAtt: historic[historic.length - 1]
      }

      products.push(newProduct)
      localStorage.setItem('products', JSON.stringify(products))

      setProduct('')
      setCodRastreio('')
      setHistoric([])
      
      takeProducts()
    }
  }

  useEffect(() => {
    takeProducts();
  }, [])

  return (
    <>
      <Container>
        <HeaderHome />
        <AddForm onSubmit={registerNewProduct}>
          <TextField
            label="Produto"
            variant="outlined"
            className={classes.input}
            value={product}
            onChange={event => setProduct(event.target.value)}
          />
          <TextField
            label="Código de Rastreio"
            variant="outlined"
            className={classes.input}
            value={codRastreio}
            onChange={event => setCodRastreio(event.target.value)}
          />
          <TextField
            label="CEP de Destino"
            variant="outlined"
            className={classes.input}
            value={cepDestino}
            onChange={event => setCepDestino(event.target.value)}
          />
          <Button type='submit' className={classes.button}>
            Adicionar
          </Button>
        </AddForm>
        <RowAnounceAndGrid>
          <GridCards>
            {products.map(product => {
              return (
                <CardItem key={product.id} product={product} handleOpen={handleOpenHistoric} />
              )
            })}
          </GridCards>
        </RowAnounceAndGrid>
      </Container>
      {selectedProduct && <ModalHistoric openHistoric={openHistoric} handleClose={handleCloseHistoric} product={selectedProduct} historic={selectedHistoric} />}
    </>
  );
}

export default Home;